import {Dispatch} from "redux";
import {oneRepoAction, oneRepoActionsTypes} from "../types/oneRepo";
import axios from "axios";

export const oneRepo = (name: string,user: string ) => async (dispatch:Dispatch<oneRepoAction>) => {
    dispatch({type:oneRepoActionsTypes.FETCH_ONE_REPO})
    try {
        const url = `https://api.github.com/repos/${user}/${name}`
        const result = await axios.get(url)
        dispatch({type:oneRepoActionsTypes.FETCH_ONE_REPO_SUCCESS,payload:result.data})
    }catch (e) {
        dispatch({type:oneRepoActionsTypes.FETCH_ONE_REPO_ERROR,payload: "Репозиторий не найден , введите полное название"})
    }
}