export enum oneRepoActionsTypes {
    FETCH_ONE_REPO="FETCH_ONE_REPO",
    FETCH_ONE_REPO_SUCCESS="FETCH_ONE_REPO_SUCCESS",
    FETCH_ONE_REPO_ERROR="FETCH_ONE_REPO_ERROR"
}
interface FetchOneRepoAction {
    type: oneRepoActionsTypes.FETCH_ONE_REPO
}
interface FetchOneRepoSuccessAction {
    type: oneRepoActionsTypes.FETCH_ONE_REPO_SUCCESS,
    payload: any[]
}
interface FetchOneRepoErrorAction {
    type: oneRepoActionsTypes.FETCH_ONE_REPO_ERROR,
    payload: string | null
}

export type oneRepoAction = FetchOneRepoAction | FetchOneRepoSuccessAction | FetchOneRepoErrorAction

export interface oneRepoState {
    repo: any | null,
    loading: boolean,
    error: null | string
}