import React, {ChangeEvent, useEffect} from 'react';
import {useAppDispatch, useAppSelector} from "../../../shared/hooks/redux-hooks";
import {fetchRepo} from "../../../redux/action-creator/repo";
import RepoListEl from "../RepoListEl";

import styles from "./RepoList.module.scss"
import SearchForm from "../SearchForm";
import {oneRepo} from "../../../redux/action-creator/oneRepo";

interface Props {
    userName: string
}

const RepoList: React.FC<Props> = ({userName}) => {
    const dispatch = useAppDispatch()

    const {repo, loading, error} = useAppSelector(state => state.repo)
    const {repo: reposit, loading: isLoading, error: isError} = useAppSelector(state => state.oneRepo)

    const handleChange = ({target}: ChangeEvent<HTMLInputElement>) => {
        setTimeout(() => {
            dispatch(oneRepo(target.value, userName))
        }, 1000)
    }

    useEffect(() => {
        dispatch(fetchRepo(userName))
    }, [dispatch, userName])

    return (
        <>
            <form>
                <input className={styles.input_search_repo} placeholder={"Name repository "}
                       onChange={(e) => handleChange(e)}/>
            </form>
            {isError && <p>{isError}</p>}
            <ul className={styles.repo_list}>
                {reposit !== null && <RepoListEl {...reposit}/>}
                {reposit === null && repo?.map(el => <RepoListEl key={el.node_id} {...el}/>)}
            </ul>
        </>
    );
};

export default RepoList;