import {oneRepoAction, oneRepoState, oneRepoActionsTypes} from "../types/oneRepo";

const initialState: oneRepoState = {
    repo: null,
    loading: false,
    error: null
}

export const oneRepoReducer = (state = initialState, action: oneRepoAction): oneRepoState => {
    switch (action.type) {
        case oneRepoActionsTypes.FETCH_ONE_REPO:
            return {repo: null, error: null, loading: true}
        case oneRepoActionsTypes.FETCH_ONE_REPO_SUCCESS:
            return {repo: action.payload, error: null, loading: false}
        case oneRepoActionsTypes.FETCH_ONE_REPO_ERROR:
            return {repo: null, loading: false, error: action.payload}
        default:
            return state
    }
}